			let base_ = 1;//60;
			let timestamp = parseInt( ( new Date() )/1000 );
			let cutoffTime = timestamp + parseInt(_.cutoffTime)*60*base_
			let finalizeTime = cutoffTime + parseInt(_.finalizeTime)*60*base_
			console.log("qSON", qSON,
				"\ncutoffTime:::"+cutoffTime,
				"\finalizeTime:::"+finalizeTime);

			let API = wagerAPI.newWagering(qSON, cutoffTime, finalizeTime, 2*24*60*60/*an hour if it hasn't been resolved*/, bonus_opts.length)
