			<hr>
			<button class="green-button" v-for="(watcher, wIndex) in watchersBacked" v-if="watchersBacked[wIndex].youBacked>0 && watchersBacked.length>0" v-on:click="openBackedWatcherBox(wIndex)">{{watcher.address.substr(0,8)}}<br>&Xi;{{ showNumber(watchersBacked[wIndex].dividends ,1e18,7) }}</button>
			<button id="newwatcher-button" class="green-button" v-on:click="openNewWatcherBox">+Watcher</button>
			<modal v-if="watchersBacked.length>0" id="backedwatcher-box" name="backedwatcher-box" :width="420" :height="360" >
				<div class="modal-boxxer">
