					<br>earnings: {{ showNumber( watchersBacked[watcherIndex].dividends ,1e18,4) }}
					<div class="doubles">
						<div>
							<contract-input label="Stake `color" commit="" placeholder="e.g. 100" :onChange="updateAmountToStake"/>
							<contract-button action="oracle_backCandidate" :data="{address:watchersBacked[watcherIndex].address, amount:amountToStake}">Stake</contract-button>
