						<contract-input label="`color to Stake from Wallet" commit="colorResolvesToStakeAndBack" placeholder="e.g. 100"/>
						<contract-input label="Candidate" commit="newWatcherToBack" placeholder="e.g. 0xabcdef123456"/>
