		  						<input title="Vote for Response Window" placeholder="e.g. 120 seconds" v-model="opt_responseWindow"/>
		  						<contract-data label="Your Suggestion" :value="yourOracleConfigVotes_responseWindow" />
		  					</div>
		  					<div class="governance_configuration">
		  						Delegate Reward Share: {{ showNumber(oracleConfigurations_delegateRS,1e18,0) }}
		  						<input title="Vote for Delegate Reward Share" placeholder="e.g. 30(%)" v-model="opt_delegateRS"/>
