		  						<input title="Vote for Service Fee" placeholder="e.g. 0.015 ether" v-model="opt_serviceFee"/>
		  						<contract-data label="Your Suggestion" :value="showNumber(yourOracleConfigVotes_serviceFee,1e18,4)" />
