		  						<input title="Vote for Base Fee" placeholder="e.g. 0.15 ether" v-model="opt_baseFee"/>
		  						<contract-data label="Your Suggestion" :value="showNumber(yourOracleConfigVotes_baseFee,1e18,4)" />
		  					</div>
		  				</div>
		  				<contract-button action="oracle_governance"
