				<br><span v-if="ticket.phase==1">In Reveal Phase</span><span v-if="ticket.phase>1">Reveal Phase Over</span> <span class="checkmark" v-if="ticket.step>1">Revealed &check;</span>
				<br><span v-if="ticket.phase==2">Ready to Finalize</span><span v-if="ticket.phase>2">Finalized<span class="checkmark" v-if="ticket.step>2">&check;</span></span>
