<script>
	import { mapState } from 'vuex'
	import ControlBox from '@/components/Control-Box'
	import ContractData from '@/components/Contract-Data'
	import ContractInput from '@/components/Contract-Input'
	import ContractButton from '@/components/Contract-Button'
	import NameRow from '@/components/Name-Row'
	import hodlConvert from 'convert-seconds'
	import Web3 from 'web3'
	let Big = require('big.js')
	
	export default {
		name: 'portals',
		created(){
			this.api_key = localStorage.getItem('api_key');
		},
		data(){return{
			/*ethIcon:require('@/assets/eth-icon.png'),
			bondsIcon:require('@/assets/bonds-icon.png'),
			estimateBonds(){this.$store.dispatch("estimateBonds")},*/
			requestDataType:'1',
			openOracles:false,
			deposit_mode:true,
			jectiveType:'false',
			query:'',
			api_key:'',
			watcherIndex:0,
			wagerIndex:0,
			ticketIndex:0,
			ticketResponse: "",
			ticketRejectionDecision: "",
			amountToStake:0,
			amountToPull:0,
			opt_roundtableSize:'',
			opt_responseWindow:'',
			opt_delegateRS:'',
			opt_freezeTime:'',
			opt_baseFee:'',
			opt_serviceFee:'',
			web3: Web3,
			/*
			<br> Query: <input type="text" placeholder="Query" />
			<br> Option 1: <input type="text" placeholder="Option 1" />
			<br> Option 2: <input type="text" placeholder="Option 2" />
			<br> Option 3: <input type="text" placeholder="Optional" />
			<br> Option 4: <input type="text" placeholder="Optional" />
			<br> Cutoff time: <input type="text" placeholder="00:00" />
			<br> Finalize After: <input type="text" placeholder="00:00" />
			*/
			Query:"",
			option1:'',
			option2:'',
			option3:'',
			option4:'',
			cutoffTime:0,
			finalizeTime:0,
			eth_to_wager:0
		}},
		components: {
			ControlBox,
			ContractData,
			ContractInput,
			ContractButton,
			NameRow
			/*ContractData*/
		},
		methods: {
			portalMode(){
				this.deposit_mode= !this.deposit_mode;
			},
			updateAPIKey(){
				localStorage.setItem('api_key', this.api_key)
				this.$store.dispatch('portals_updateAPIKey', this.api_key);
			},
			toggleOracles(){
				this.openOracles= !this.openOracles;
			},
			set_ticketResponse(x){
				this.ticketResponse= x;
			},
			openNewWatcherBox(){
				this.$modal.show("newwatcher-box")
			},
			formatTime(hodl, labelSeconds){
		        //
				//console.log("hodl",hodl)
				let hodlC = hodlConvert(hodl);
				if(hodlC.hours>24*3){
				  let hourLeft = hodlC.hours % 24;
				  let days = Math.floor(hodlC.hours / 24);

				  return days + ' Day'+(days>1?'s':'')+' ' +hourLeft + ' H'
				}
				else{
				  if(hodl>60*60)
				    return ( (hodlC.hours>0)?(hodlC.hours + 'H '):'') +hodlC.minutes + 'M'
				  else{
				    if(hodl>60*10)
				      return hodlC.minutes + 'M'
				    else{
				      return (hodlC.minutes>0?(hodlC.minutes + 'M ' ):''  )+ (hodl%60) + (labelSeconds?' seconds':'')
				    }
				  }
				}
			},
			openBackedWatcherBox(wIndex){
				this.watcherIndex = wIndex;
				this.$modal.show("backedwatcher-box")
			},
			openWagerBox(wIndex){
				this.wagerIndex = wIndex;
				this.$modal.show("betting-box")
			},
			showNumber(x,dec,toFix){
				if(!dec){ dec = 1; toFix=0; }
				let bigR = Big(x.toString())
				bigR = bigR.div(dec)
				let XZ = bigR.toFixed(toFix)
				return XZ;
			},
			openTicketBox(tIndex){
				this.ticketIndex = tIndex;
				if( this.tickets[tIndex].responded ){
				  this.ticketResponse = this.tickets[tIndex].response
				  this.ticketRejectionDecision = this.tickets[tIndex].rejectionDecision
				}else{
				  this.ticketResponse = ''  
				  this.ticketRejectionDecision = ''
				}
				this.$modal.show("ticket-box")
			},
			closeGovernanceBox(	){
				this.$modal.hide("governance-box")
			},
			openBox(id){
				this.$modal.show(id)
			},
			updateAmountToStake(x){
				this.amountToStake = x;
			},
			updateAmountToPull(x){
				this.amountToPull = x;
			},
			submitRequestTicket(){
				console.log("this.query", this.query)
				console.log("this.requestDataType", this.requestDataType)
				console.log("this.jectiveType", this.jectiveType)
			},
			saveResponse(){
				console.log("TEST")
				this.$modal.hide("ticket-box")
				this.$store.commit("save_response_into_storage", {ticketIndex:this.ticketIndex, ticketResponse:this.ticketResponse, ticketRejectionDecision: this.ticketRejectionDecision})
			}
		},
		computed:{
			...mapState([
				"yourCandidate",
				"yourSuggestedMR",
				"masternodeRequirement",
				"communityFund",
				"yourGateway",
				"communityResolve",
				"unlockedResolves",
				"yourRegularBonds",
				"yourRegularResolves",
				"yourBonds",
				"yourResolves",
				"nameList",
				"colorResolvesToStakeAndBack",
				"newWatcherToBack",
				"watcherToBack",
				"resolvesInOracle",
				"resolvesFree",
				"resolvesLocked",
				"watchersBacked",
				"roundtableWatchers",
				"colorResolvesToStake",
				"yourOracleConfigVotes_roundTableSize",
				"yourOracleConfigVotes_responseWindow",
				"yourOracleConfigVotes_delegateRS",
				"yourOracleConfigVotes_freezeTime",
				"yourOracleConfigVotes_baseFee",
				"yourOracleConfigVotes_serviceFee",
				"oracleConfigurations_roundTableSize",
				"oracleConfigurations_responseWindow",
				"oracleConfigurations_delegateRS",
				"oracleConfigurations_freezeTime",
				"oracleConfigurations_baseFee",
				"oracleConfigurations_serviceFee",
				"cashoutTotal",
				"oracleFee",
				"TIMESTAMP",
        		"tickets",
        		"wager_wagers",
        		"portal_asset",
        		"portal_amount"
			])
		}
	}
</script>

<style scoped>
.grid-layout{ 
	display: grid;
	grid-gap: 10px;
	grid-template-columns: auto auto auto;
}
#global-grid{
	display: grid;
	grid-template-columns: auto auto; 
}
h2{text-align:center;}
.oracle-stuff{
	grid-column: 1 / span 3;
	margin-bottom:30px;
}
.doubles{
	display: grid;
