	transform: translate(0px, 10px);
}
.rountable-watcher{
  border-style: solid;
  display: inline-block;
  padding:5px;
}
#unstake-looseresolves-button{width:150px;}
.modal-boxxer{padding:20px;box-sizing: content-box; border:solid; border-width:1px;
	background:#1a1a1a;
	height:100%;}
.checkmark{font-weight:800;color:green;}
#under-construction {background:#ff9900;}
#under-construction h5{text-align:center;}
#under-construction img{display:block; margin: 0px auto; width:120px;}
.green-button{
	width:100px;
	height:28px;
	cursor:pointer;
	text-decoration: none;
    border-color:#007e08;
    border-width:1px;
    background:none;
    border-radius: 15px;
    margin-top:10px;
    color:white;
    font-family: 'Russo One', sans-serif;
}

button.boldoption{
	font-weight:800;
}
#portal-box{
	display:grid;
	grid-template-columns: 33% 33% 33%;
