	margin:0 auto;
	display:block;

}
#wormhole{
	width:59px;
	height:42px;
}

.thicc{
  height:36px;
}
.extra-thicc{
  height:75px;
  font-size:100%;
}
.switched{
	transform: scaleY(-1);
}
#catch-assets h5{
