<template>
  <div class="control-box">
    <h4>{{title}}</h4>
    <div class="slot-wrap">
      <slot></slot>
    </div>
  </div>
</template>

<script>

export default {
  name: 'control-box',
  props:{
    title: String
  }
}
</script>

<style scoped>
.control-box{
  display: block;
  border-bottom-style:solid;
  border-right-style:solid;
  border-width: 1px;
  float:left;
  box-sizing: content-box;
  background: white;
  overflow:hidden;
}
.contract-box{
  margin-top:5px;
  margin-bottom:5px;
}
.slot-wrap{
  padding:20px;
}
.control-box h4{
  font-size:25px;
  font-family: 'Russo One', sans-serif;
  font-weight:100;
  text-indent: 20px;
  margin:0;
  padding-top: 10px;
  padding-bottom: 5px;
  background: #ccc;
}

#exchange .control-box{
  background: none;
  color: #85fd97;
  border-color: #007e08;
  border-style-bottom: solid;
  font-family: Lucida Console;
}

.phoam #exchange .control-box{
  border-color: #256e6f;
  color: #fff;
}

#exchange .control-box h4{
  background: none;
  background-repeat: no-repeat;
  /*border-bottom: 1px solid #007e08;*/
  text-decoration:underline;
  color:#85fd97;
}

.phoam #exchange .control-box h4{
  color:#256e6f;
  margin-top:15px;
  padding-left:10px;
  text-decoration:none;
  text-shadow: 2px 2px #a7e1e2;
}


</style>



