<template>
  <div id="exchange" class="grid-layout">
    <control-box id="buy-box" class="box--gradient platinum second" :title="'Buy '+ BONDS">
      <contract-input :label="CURRENCY+' to Spend'" placeholder="e.g. 0.12" commit="ethToSpend" />
      <contract-button action="buyACRE">Buy $ACRE</contract-button>
    </control-box>
    <control-box id="passive-earnings" class="box--gradient platinum second" :title="'Passive Earnings'">
      <div class="double-grid">
        <div>
          <contract-data label="$ACRE Weight" :value="ACRE_balance" :img="acreIcon" />
        <contract-button action="greenpoint_withdraw" :data="{pool:false}">Pull &Xi;{{showNumber(acreDividends,1e18,3)}}</contract-button>
        </div>
        <div>
          <contract-data label="TOTEM Weight" :value="totemWeight" :img="stackIcon" />
          <contract-button action="greenpoint_withdraw" :data="{pool:true}">Pull {{showNumber(totemDividends,1e18,3)}} MVT</contract-button>
        </div>
      </div>
    </control-box>
    <control-box id="sell-box" class="box--gradient platinum second with-label" :title="'Sell MVT'">
      <div class="double-grid">
        <div>
          <contract-button v-if="!MVT_approval" action="greenpoint_approveMVT">Approve MVT</contract-button>
          <contract-data v-if="MVT_approval" label="MVT tokens" :value="MVT_balance" :img="resolveIcon" />
        </div>
        <div>
          <contract-data label="Floor Price &amp; Reserve" :value="MVT_floorPrice +' / '+MVT_floorReserve " :img="resolveIcon" />
        </div>
      </div>

      <contract-input v-if="MVT_approval" :label="'MVT to Sell'" placeholder="e.g. 12.00" commit="mvtToSell" />
      <contract-button v-if="MVT_approval" action="greenpoint_sellMVT">Sell MVT</contract-button>
    </control-box>
    <control-box id="nft-converter" class="box--gradient platinum second" :title="'Create TOTEM'">
      <div class="double-grid">
        <div>
          <contract-button v-if="!MDT_approval" action="greenpoint_approveMDT">Approve MDT</contract-button>
          <contract-data v-if="MDT_approval" label="MDT tokens" :value="MDT_balance" :img="resolveIcon" />
          <contract-input v-if="MDT_approval" :label="BONDS+' to Solidify'" placeholder="e.g. 1.05" commit="acreToSolidify" />
          <contract-button v-if="MDT_approval" action="greenpoint_createTotem">Create TOTEM</contract-button>
        </div>
      </div>
    </control-box>
    <control-box id="totems-list" class="box--gradient platinum second" :title="'TOTEMs'">
      <div v-if="TOTEMs.length>0">
        <div v-for="TOTEM in TOTEMs"  v-if="TOTEM.owned">
          TOTEM #{{TOTEM.totemID}} <img class="acre-image" src="@/assets/acre.png"/>
          {{showNumber(TOTEM.acreWeight,1e18,3)}}
          <contract-button class="totem-button" v-if="TOTEM.manifestEarnings>0 && !TOTEM.manifested && TOTEM.owned && TOTEM.currentlyStaking" action="greenpoint_manifest" :data="{totemID: TOTEM.totemID}">Manifest {{showNumber(TOTEM.manifestEarnings,1e18,3)}}</contract-button>
          <contract-button class="totem-button" v-if="TOTEM.currentlyStaking && !TOTEM.manifested" action="greenpoint_destroyTotem" :data="{totemID: TOTEM.totemID}">Dissolve</contract-button>
          <contract-button class="totem-button" v-if="TOTEM.currentlyStaking && TOTEM.staked" action="greenpoint_unstakeNFT" :data="{totemID: TOTEM.totemID}">Unstake</contract-button>
          <contract-button class="totem-button" v-if="TOTEM.owned && !TOTEM.staked" action="greenpoint_stakeNFT" :data="{totemID: TOTEM.totemID}">Stake</contract-button>
          <span v-if="!TOTEM.staked && !TOTEM.manifested">Potential: {{showNumber(TOTEM.manifestEarnings,1e18,3)}} </span>
          <span v-if="TOTEM.frozen">Manifesting...</span>
          <span v-if="TOTEM.manifested">Manifest: {{showNumber(TOTEM.manifestEarnings,1e18,3)}} MVT</span>
        </div>
      </div>
    </control-box>
    
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  import ControlBox from '@/components/Control-Box'
  import ContractData from '@/components/Contract-Data'
  import ContractInput from '@/components/Contract-Input'
  import ContractButton from '@/components/Contract-Button'
  import Kingdom from '@/components/Kingdom'
  import VueCharts from 'vue-chartjs'
  import ColorPicker from 'vue-color-picker-wheel'
  import hodlConvert from 'convert-seconds';
  let Big = require('big.js')

  export default {
    name: 'greenpoint',
    created(){
      this.$store.dispatch("updateCycle")
    },
    data(){
      let DATA = {
        CURRENCY:"ETH",
        BONDS:"$ACRE",
        ethIcon:require('@/assets/eth-icon.png'),
        balloonsIcon:require('@/assets/kingdoms/balloons.png'),
        resolveIcon:require('@/assets/color-coins.png'),
        acreIcon:require('@/assets/acre.png'),
        stackIcon:require('@/assets/stack.png'),
        potentialIcon:require('@/assets/painting-with-color-tokens.png'),
        clockIcon:require('@/assets/clock.png'),
        feeIcon:require('@/assets/resolver-fee-icon.png'),

        R:Math.floor(Math.random()*4),
        G:Math.floor(Math.random()*4),
        B:Math.floor(Math.random()*4),
        K: 0,
        PiZZaToFomo: 0,
        rugsToTrade: 0,
        ColorToConquer: 0,
        additionalTimeEstimation:0,
        selectedKingdomForImports: '',
        jerkyToBuy: 0
      }
      for(var i=0; i<8; i+=1){ DATA['market_'+i] = require('@/assets/kingdoms/market-'+i+'.png') }
      return DATA;
    },
    methods: {
      formatTime(hodl, labelSeconds){
        //
        //console.log("hodl",hodl)
        let hodlC = hodlConvert(hodl);
        if(hodlC.hours>24*3){
          let hourLeft = hodlC.hours % 24;
          let days = Math.floor(hodlC.hours / 24);

          return days + ' Day'+(days>1?'s':'')+' ' +hourLeft + ' H'
        }
        else{
          if(hodl>60*60)
            return ( (hodlC.hours>0)?(hodlC.hours + 'H '):'') +hodlC.minutes + 'M'
          else{
            if(hodl>60*10)
              return hodlC.minutes + 'M'
            else{
              return (hodlC.minutes>0?(hodlC.minutes + 'M ' ):''  )+ (hodl%60) + (labelSeconds?' seconds':'')
            }
          }
        }
      },
      RATION_COST(){        
        return this.showNumber(this.R99_rationCost,1e18,3)
      },
      colorMatch(K,r,g,b){
        return K.R == r && K.G == g && K.B == b
      },
      isFloatingCastle(Ki){
        return this.rugsToWishes_kingdoms[Ki].address == this.rugsToWishes_floatingCastleAddress
      },
      marketImage(n){
        return this['market_'+n%8];
      },
      onColorChange(color) {
        console.log("COLOR_____",color)
        this.$store.dispatch("estimateBonds")
        this.$store.commit("update_buyColor", color)
      },
      clusterColor(r,g,b){
        //
        function componentToHex(c) {
          var hex = c.toString(16);
          return hex.length == 1 ? "0" + hex : hex;
        }

        function rgbToHex(r,g,b) {
          return "#" + componentToHex(r) + componentToHex(g) + componentToHex(b);
        }
        let cluster = 255/3;

        let xxx;

        if(r!=6)
          xxx = rgbToHex( parseInt(cluster * r) , parseInt(cluster * g), parseInt(cluster * b) );
        else
          xxx = rgbToHex( parseInt(255/2) ,parseInt(255/2), parseInt(255/2) );

        this.$store.commit("update_buyColor", xxx )
      },
      openColorBox(){
        this.$modal.show("color-box")
      },
      openBalloonBox(){
        this.$modal.show("balloon-box")
      },
      openKingdomBox(k){
        console.log("K",k)
        this.K = k
        this.$modal.show("kingdom-box")
      },
      withdrawRugBux(){
        this.$store.dispatch("rugpull_withdraw") 
      },
      attackCarpetRider(){
        this.$store.dispatch("attack_carpetRider") 
      },
      claim_kingdom(r,g,b){
        this.$store.dispatch("rugsToWishes_claimKingdom",{R:r,G:g,B:b}) 
      },
      ///
      getClaimColor(){
        function componentToHex(c) {
          var hex = c.toString(16);
          return hex.length == 1 ? "0" + hex : hex;
        }

        function rgbToHex(r, g, b) {
          return "#" + componentToHex(r) + componentToHex(g) + componentToHex(b);
        }
        let cluster = 255/3;
        return rgbToHex( parseInt(cluster * this.R) , parseInt(cluster * this.G), parseInt(cluster * this.B) );
      },
      kingdomColor(R,G,B){
        function componentToHex(c) {
          var hex = c.toString(16);
          return hex.length == 1 ? "0" + hex : hex;
        }

        function rgbToHex(r, g, b) {
          return "#" + componentToHex(r) + componentToHex(g) + componentToHex(b);
        }
        let cluster = 255/3;
        if(R != 6)
          return rgbToHex( parseInt(cluster * R) , parseInt(cluster * G), parseInt(cluster * B) );
        else
          return rgbToHex( parseInt(255/2),parseInt(255/2),parseInt(255/2));
      },
      rugsToWishes_becomeGenie(){
        this.$store.dispatch("rugsToWishes_becomeGenie") 
      },
      address(a,b){
        return a.toLowerCase() == b.toLowerCase();
      },
      updatePiZZaToFomo(x){
        console.log("the amount of pizza",x)
        console.log("this.rugsToWishes_clockPressure",this.rugsToWishes_clockPressure)
        let pETH;
        pETH = x * 1e18 * (this.bondValue / this.bondCount)
        console.log( "pETH", pETH)
        console.log("( this.rugsToWishes_clockPressure + pETH )", ( parseInt(this.rugsToWishes_clockPressure) + pETH )     )

        let estimation = this.hodl / (  ( parseInt(this.rugsToWishes_clockPressure) + pETH ) / pETH)

        this.additionalTimeEstimation = Math.floor(estimation);
        this.PiZZaToFomo = x;
      },
      updateColorToConquer(x){
        this.ColorToConquer = x;
      },
      updateRugsToTrade(x){
        this.rugsToTrade = x;
      },//,//updateColorToConquer
      fomoIN(x){
        this.$store.dispatch("rugsToWishes_fomoIN", {kingdom:x, pizza: this.PiZZaToFomo} )
      },
      conquer(x){
        this.$store.dispatch("rugsToWishes_conquer", {kingdom:x, color: this.ColorToConquer} )
      },
      //conquer
      tradeRugs(x){
        this.$store.dispatch("rugsToWishes_tradeRugs", {kingdom:x, rugs: this.rugsToTrade} )
      },
      importRugs( importedColored ){
        this.$store.dispatch("rugsToWishes_importRugs", {kingdom:this.selectedKingdomForImports, color:importedColored.address, rugs: this.rugsToTrade} )
      },
      rugAttackGenie(x){
        this.$store.dispatch("rugsToWishes_rugAttackGenie", this.rugsToTrade )
      },
      exitScam(x){
        this.$store.dispatch("rugsToWishes_exitScam", {kingdom:x} )
      },
      getPiZZaDivis(x){
        this.$store.dispatch("rugsToWishes_pizzaDivis", {kingdom:x.address, fomoRound: x.fomoRound} )
      },
      getCaravanDivis(){
        this.$store.dispatch("rugsToWishes_caravanDivis" )
      },
      scrapDivs(){
        this.$store.dispatch("rugsToWishes_scrapDivs" )
      },
      isMarket(kingdom){
        let x1 = this.rugsToWishes_caravanX;
        let x2 = kingdom.X;
        let y1 = this.rugsToWishes_caravanY;
        let y2 = kingdom.Y;
        /*console.log("-=-=-=-=-=-==-=-=-\n-=-=-=-=-=-=-=-=-=-\n \n\nkingdom.x",kingdom.X,"\n\nkingdom.y",kingdom.Y,"\n\nthis.rugsToWishes_caravanX",this.rugsToWishes_caravanX,"\n\nthis.rugsToWishes_caravanY",this.rugsToWishes_caravanY)
        */
        let X = x1-x2<0?(x2-x1):(x1-x2);
        let Y = y1-y2<0?(y2-y1):(y1-y2);
        return ( ( ( X + Y ) == 1 ) || (( X + Y )==2 && x2 != x1 && y1 != y2) ) && this.rugsToWishes_floatingCastleAddress != kingdom.address;
      },
      proximity(K1,K2,_4_8){
        let x1 = this.rugsToWishes_kingdoms[K2].X;
        let x2 = this.rugsToWishes_kingdoms[K1].X;
        let y1 = this.rugsToWishes_kingdoms[K2].Y;
        let y2 = this.rugsToWishes_kingdoms[K1].Y;
        /*console.log("-=-=-=-=-=-==-=-=-\n-=-=-=-=-=-=-=-=-=-\n \n\nkingdom.x",kingdom.X,"\n\nkingdom.y",kingdom.Y,"\n\nthis.rugsToWishes_caravanX",this.rugsToWishes_caravanX,"\n\nthis.rugsToWishes_caravanY",this.rugsToWishes_caravanY)
        */
        let X = x1-x2<0?(x2-x1):(x1-x2);
        let Y = y1-y2<0?(y2-y1):(y1-y2);
        return ( ( ( X + Y ) == 1 ) || ( _4_8 && ( X + Y )==2 && x2 != x1 && y1 != y2 ) ) && this.rugsToWishes_floatingCastleAddress != kingdom.address;
      },
      showNumber(x,dec,toFix){
        if(!dec){ dec = 1; toFix=0; }
        let bigR = Big(x.toString())
        bigR = bigR.div(dec)
        let XZ = bigR.toFixed(toFix)
        return XZ;
      }
    },
    components: {
      ControlBox,
      ContractData,
      ContractInput,
      ContractButton,
      ColorPicker,
      Kingdom
    },
    computed:{
      ...mapState([
        "MDT_approval",
        "MVT_approval",
        "MDT_balance",
        "MVT_balance",
        "ACRE_balance",
        "totemWeight",
        "MVT_distributionPot",
        "MVT_floorReserve",
        "MVT_nextFloorRaisingTime",
        "MVT_floorPrice",
        "totalTotemWeight",
        "totalAcreWeight",
        "totalAcreSupply",
        "acreDividends",
        "totemDividends",
        "TOTEMs",
//--------------------------------------------
//--------------------------------------------
//--------------------------------------------
//--------------------------------------------
//--------------------------------------------
        "totalBondSupply",
        "totalStakedResolves",
        "ethInReserve",
        "resolveFee",
        "ethToSpend",
        "buyPrice",
        "sellPrice",
        "yourBonds",
