        "currentAddress",
        "rugsToWishes_floatingCastle",
        "rugsToWishes_floatingCastleIndex",
        "rugsToWishes_floatingCastleAddress",
        "rugsToWishes_kingdoms",
        "rugsToWishes_unclaimed",
        "rugsToWishes_PiZZaClusterR",
        "rugsToWishes_PiZZaClusterG",
        "rugsToWishes_PiZZaClusterB",
        "rugsToWishes_ResolveClusterR",
        "rugsToWishes_ResolveClusterG",
        "rugsToWishes_ResolveClusterB",
        "TIMESTAMP",
        "rugsToWishes_caravanWeight",
        "rugsToWishes_caravanTotalWeight",
        "rugsToWishes_caravanPercent",
        "rugsToWishes_caravanDividends",
        "rugsToWishes_caravanColor",
        "rugsToWishes_caravanRug",
        "rugsToWishes_caravanRugs",
        "rugsToWishes_scrapDivis",
        "rugsToWishes_caravanX",
        "rugsToWishes_caravanY",
        "rugsToWishes_caravanLocation",
        "rugsToWishes_hasKingdoms",
        "exactResolves",
