<style scoped>
.control-box{border-radius: 6px;}
.grid-layout{
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 50% 50%;
}
#global-grid{
  display: grid;
  grid-template-columns: auto auto; 
}
.double-grid{
  display: grid;
  grid-template-columns: auto auto; 
}
#buy-box{
  position: relative;
}
#bond-color-picker{
  width: 32px;
  height: 24px;
  display: block;
  left:110px;
  transform: translate(56px,-37px);
  position: absolute;
  cursor:pointer
}
#color-box .color-modal{
  width:320px;
  height:240px;
  padding: 20px;
}
.color-indicator{
  padding-left: 3px;
  border-style:solid;
  border-width:1px;
  height:28px;
  line-height: 28px;
  max-width:100px;
}
.gap{padding: 3px;}
.color-indicator img{
  width:24px;
  display: inline;
  margin-right: 3px;
}
h4,h5,h6{
  margin:0;
}

.wide-box{
  grid-column: 1 / span 2;
}
  @media only screen and (max-width: 922px) {
    .grid-layout{
      grid-template-columns: auto auto;
    }
    .wide-box{
      grid-column: 1 / span 2;
    }
  }
  @media only screen and (max-width: 668px) {
    .grid-layout{
      grid-template-columns: auto;
    }
    .wide-box{
      grid-column: 1 / span 1;
    }
  }

#comfy-rug{
  padding-top:80px;
  text-align:center;
  font-family: 'Lemonada', sans-serif;
  color:white;
  font-size:20px;
}


#rugpull-game .white{
  font-family: 'Lemonada', sans-serif;
  color:white;
  font-size:16px; 
}

#rug-bux{
  position:absolute;
  top:36px;
  right:5px;
  width: 130px;
  height: 40px;
  color: #fff;
  border-radius: 5px;
  font-family: 'Lato', sans-serif;
  font-weight: 500;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-block;
   box-shadow:inset 2px 2px 2px 0px rgba(255,255,255,.5),
   7px 7px 20px 0px rgba(0,0,0,.1),
   4px 4px 5px 0px rgba(0,0,0,.1);
  outline: none;
  background: rgb(255,151,0);
  border: none;
  z-index: 1;
}


#rug-bux:after {
  position: absolute;
  content: "";
  width: 100%;
  height: 0;
  top: 0;
  left: 0;
  z-index: -1;
  border-radius: 5px;
  background-color: #eaf818;
  background-image: linear-gradient(315deg, #eaf818 0%, #f6fc9c 74%);
  transition: all 0.3s ease;
}
#rug-bux:hover {
  color: #000;
}
#rug-bux:hover:after {
  top: auto;
  bottom: 0;
  height: 100%;
}
#rug-bux:active {
  top: 2px;
}
.relative{
  position:relative;
}

#rugpull-game{
  background: url("../assets/rugpull-desert.png");
  background-repeat: no-repeat;
  background-size: cover; /* Resize the background image to cover the entire container */
  background-position: left center;
}
#rug-token{
  width:64px;
  filter: invert(1);

  display:block;
  margin:0 auto;
  margin-bottom:5px;
  position:relative;
  right:10px;

}

#rugpull-grid{
  display:grid;
  grid-template-columns: auto auto auto;
}

.btn-hover {
    width: 120px;
    font-size: 13px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    margin: 5px;
    height: 25px;
    text-align:center;
    border: none;
    background-size: 300% 100%;

    border-radius: 15px;
    moz-transition: all .4s ease-in-out;
    -o-transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
}

.btn-hover:hover {
    background-position: 100% 0;
    moz-transition: all .4s ease-in-out;
    -o-transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
}

.btn-hover:focus {
    outline: none;
}

.btn-hover.color-3 {
    background-image: linear-gradient(to right, #667eea, #764ba2, #6B8DD6, #8E37D7);
    box-shadow: 0 4px 15px 0 rgba(116, 79, 168, 0.75);
}
