<template>
  <div class="contract-input contract-box">
    <img v-if="img" :src="img"/>
    <div class="right">
      <h6 class="little-text">{{label}}</h6>
      <input type="text" :placeholder="placeholder" :value="val" @input="updateState" />
    </div>
  </div>
</template>

<script>

export default {
  created(){
  },
  name: 'contract-input',
  /*data(){return{
    onChange:function(){console.log('asldkjf')}
  }},*/
  props:[
    'label',
    'placeholder',
    'img',
    'commit',
    'onChange',
    'str'
  ],
  computed:{
    val(){
      if(this.commit){
        let state = this.$store.state[this.commit];
        if(state === 0)
          return ""
        else
          return state
      }
    }
  },
  methods:{
    updateState(e){
      if(this.commit){
        let value = e.target.value
        if ( value === '' && !this.str)
          value = '0'

        //console.log("What's in $store: ",this.$store._mutations)

        if(this.$store._mutations["update_"+this.commit])
          this.$store.commit("update_"+this.commit, value)
        else//I know this is unholy
          this.$store.commit("update_", {VAR:this.commit, value:value})
        
      }
      
      if (this.onChange){
        this.onChange(e.target.value)
      }
    }
  }
}

</script>

<style scoped>
.contract-data > img{
  max-width:32px;
}
h6{
  margin:0;
  padding:0;
}
h6.little-text{
  color:#009e08;
}
.phoam h6.little-text{
  color:#256e6f;
}

input{
  background:none;
  border-style:solid;
  border-width:1px;
  border-color:#009e08;
  color:#85fd97;
}

.phoam input{
  border-color:#256e6f;
  color:#256e6f;
}
</style>



