        <nav id="contract-tab-nav">
          <!-- <button v-if="CHAIN==1 || CHAIN==0" v-on:click="tabSelect(3)">Greenpoint</button>  -->
          <!-- <button v-on:click="tabSelect(1)">Oracle</button>  -->
        </nav>
        <div id="exchange-page">
          <metamask-gateway>
            <!--<exchange v-if="tab==1"/>-->
            <greenpoint/>
          </metamask-gateway>
          <!--<div id="masternode" v-if="currentAddress!=null">
            Affiliate Link<br>
            <input :value="'http://powhr.io/'+currentAddress" readonly/>
          </div>-->
          <!--<div id="masternode" v-if="currentAddress!=null">
            <input :value="'https://itsJust.PiZZa/#/'+currentAddress" readonly/><br>
            Affiliate Link ( You'll need to stake `color to benefit )
          </div>-->

        </div>
        <!-- <footer v-if="reffMode"><a :href="'https://'+ropstenDomain+'blockscout.com/etc/mainnet/address/'+rugpullAddress+'#code'" target="_blank">Verified RugPull Contract</a>| <a :href="'https://'+ropstenDomain+'blockscout.com/etc/mainnet/address/'+resolveAddress+'#code'" target="_blank">Resolve Contract</a> | <a :href="'https://'+ropstenDomain+'blockscout.com/etc/mainnet/address/'+oroclAddress+'#code'" target="_blank">Orocl Contract</a>
        </footer>-->
        <footer><!--<a href="https://obelus.voto" id="obelus">OBELUS.VOTO</a> <span class="silver">|</span> <a :href="'https://'+ropstenDomain+'blockscout.com/etc/mainnet/address/'+pyramidAddress.etc+'#code'" target="_blank">Pyramid</a> <span>|</span> <a :href="'https://'+ropstenDomain+'blockscout.com/etc/mainnet/address/'+resolveAddress.etc+'#code'" target="_blank">`rgb</a> <span>|</span> <a :href="'https://'+ropstenDomain+'blockscout.com/etc/mainnet/address/'+oroclAddress.etc+'#code'" target="_blank">Oracle</a> <span>|</span> <a :href="'https://'+ropstenDomain+'blockscout.com/etc/mainnet/address/'+rugpullAddress.etc+'#code'" target="_blank">Blockchain 4D Chess</a>-->
        <!--  <a href="https://OBELUS.VOTO" id="obelus">OBELUS.VOTO</a>-->
        </footer>
      </div>
