<script>
import Chart from 'chart.js';

import { mapState } from 'vuex'
import MetamaskGateway from '@/components/Metamask-Gateway'
import Exchange from '@/components/Exchange'

import Wagers from '@/components/Wagers'
import Portals from '@/components/Portals'
import Greenpoint from '@/components/Greenpoint'
import Oracle from '@/components/Oracle'

import Stats from '@/components/Stats'
import NavMenu from '@/components/Nav-Menu'

import pyramidAddress from '../store/addresses/pyramid'
import resolveAddress from '../store/addresses/resolve'
import oroclAddress from '../store/addresses/orocl'
import rugpullAddress from '../store/addresses/rugpull'

/*console.log("Pyramid Contract: https://blockscout.com/etc/mainnet/address/"+pyramidAddress,"\nToken Contract: https://blockscout.com/etc/mainnet/address/"+resolveAddress,"\nColor Contract: https://blockscout.com/etc/mainnet/address/"+prismAddress,"\nColor Contract: https://blockscout.com/etc/mainnet/address/"+prismAddress)*/

export default {
  name: 'contract',
  created(){
    this.$store.commit("update_urlGateway", this.$route.params.masternode)
    this.$store.dispatch("connectToMetaMask")
    this.$store.dispatch("updateCycle")
    if( this.$route.path.includes("portals") ){
      console.log("-------------PORTALS----------------")
      this.tabSelect(2)
    }else if( this.$route.path.includes("wagers") ){
      console.log("---------------WAGERS-------------")
      this.tabSelect(1)
    }else{
      console.log("---------------EXCHANGE-------------")
      this.tabSelect(0)
    }
  },
  data(){return{
      tab:3,
      ropstenDomain:"",//ropsten.
      pyramidAddress:pyramidAddress,
      resolveAddress:resolveAddress,
      oroclAddress:oroclAddress,
      rugpullAddress:rugpullAddress
  }},
  methods:{
    tabSelect(x){
      this.tab = x;
    }
  },
  components: {
    NavMenu,
    Exchange,
    Wagers,
    Oracle,
    Portals,
    Greenpoint,
    Stats,
    MetamaskGateway
  },
  computed:{
    ...mapState([
      "mode",
      "currentAddress",
      "yourGateway",
      "SKIN",
      "urlGateway",
      "reffMode",
      "CHAIN"
    ])
  }
}
