  min-height: 100vh;
  height:100%;
}

#masternode{text-align: center; font-family: verdana;margin-top:12px; color:white; background:#000000bb;}
#masternode{color:#009e08; font-family:Lucida Console;}
#masternode input{
  width:400px; font-family:Lucida Console; background:none;
  color:#85fd97;
  border-width:1px;
  border-color:#009e08;
}

@media only screen and (max-width: 668px) {
  #masternode input{width:250px;}
}


#contract footer{
  position: absolute;
  bottom:0;
  width:100%;
  text-align: center;
  padding-top: 5px;

}
#contract footer a{
  color:white;
  font-family:Lucida Console;
}
footer span{
  color:gold;
}
#contract header{
  margin-bottom:20px;
  position: relative;
}
#contract header h1{
  text-align: center;
  font-family: 'Alegreya Sans SC', sans-serif;
  font-weight: 100;

}/*
#contract header h1{
  position: absolute;
}*/
#contract-nav{
