  margin-left:10px;
  margin-right:10px;

  border-color:#007e08;
  border-width:1px;
  background:none;
  border-radius: 15px;
  margin-top:10px;

  padding:7px;
  color: #fff;
  position: relative;
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
  width:125px;
}
#contract-nav {
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-flex-wrap: nowrap;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-align-content: stretch;
    -ms-flex-line-pack: stretch;
    align-content: stretch;
    -webkit-align-items: flex-start;
    -ms-flex-align: start;
    align-items: flex-start;
    }

#contract-nav button:nth-child(1) {
    -webkit-order: 0;
    -ms-flex-order: 0;
    order: 0;
    -webkit-flex: 0 1 auto;
    -ms-flex: 0 1 auto;
    flex: 0 1 auto;
    -webkit-align-self: auto;
    -ms-flex-item-align: auto;
    align-self: auto;
    }

#contract-nav button:nth-child(2) {
    -webkit-order: 0;
    -ms-flex-order: 0;
    order: 0;
    -webkit-flex: 0 1 auto;
    -ms-flex: 0 1 auto;
    flex: 0 1 auto;
    -webkit-align-self: auto;
    -ms-flex-item-align: auto;
    align-self: auto;
    }

#contract-nav button:nth-child(3) {
    -webkit-order: 0;
    -ms-flex-order: 0;
    order: 0;
    -webkit-flex: 0 1 auto;
    -ms-flex: 0 1 auto;
    flex: 0 1 auto;
    -webkit-align-self: auto;
    -ms-flex-item-align: auto;
    align-self: auto;
    }

#contract-tab-nav button{
  width:150px;
  height:30px;
  font-size:18px;
  margin-left:5px; margin-right:5px;
  cursor:pointer;

  border-color:#007e08;
  border-width:1px;
  border-radius: 15px;
  color:white;
  background:none;
  font-family: 'Russo One', sans-serif;
}

.phoam #contract-tab-nav button{
  border-color:#256e6f;
  color:#256e6f;
  border-top-style:none;
  border-left-style:none;
}

footer a#obelus{color:#09f;font-size:70%;}
</style>



