import Vue from 'vue'
import Router from 'vue-router'
import Contract from '@/components/Contract'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Contract',
      component: Contract
    },
    {
      path: '/:masternode',
      name: 'Contract',
      component: Contract
    },
    {
      path: '/contract/:masternode',
      name: 'Contract',
      component: Contract
    },
    {
      path: '/contract',
      name: 'Contract',
      component: Contract
    },
    {
      path: '/portals',
      name: 'Portals',
      component: Contract
    },
    {
      path: '/wagers',
      name: 'Wagers',
      component: Contract
    }/*,
    {
      path: '/fundraiser',
      name: 'FundRaiser',
      component: FundRaiser
    }*/
  ]
})



