<template>
  <button class="contract-button contract-box" @click="dispatch">
    <slot></slot>
  </button>
</template>

<script>

export default {
  name: 'contract-button',
  props:{
    action: String,
    data: Object
  },
  methods:{
    dispatch(){
      if(this.action){
        if(this.data){//console.log("HITTTTTT")
          this.$store.dispatch(this.action, this.data)
        }
        else{//console.log("TESTSTETS")
          this.$store.dispatch(this.action)
        }
      }
    }
  }
}
</script>

<style scoped>

button{
  border-width:1.5px;
  border-color:#444;
  padding:7px;
  color: #444;
  position: relative;
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
  width:125px;
}

#exchange button{
  border-color:#007e08;
  border-width:1px;
  background:none;
  border-radius: 15px;
  color:white;
  font-family: 'Russo One', sans-serif;
}

.phoam #exchange button{
  border-color:#256e6f;
  color:#256e6f;
}

</style>



