<template>
  <div class="kingdom-box" v-bind:class="{ portals:kingdom.portals , colorMatch: kingdom.R == rugsToWishes_PiZZaClusterR && kingdom.G == rugsToWishes_PiZZaClusterG && kingdom.B == rugsToWishes_PiZZaClusterB, inthebox:  ( rugsToWishes_floatingCastleAddress != kingdom.address && kingdom.X>=3 && kingdom.X <=6 && kingdom.Y>=3 && kingdom.Y <=6 ) }">
    <!--<span v-bind:class="{ portals:kingdom.portals }" >{{kingdom.portals}}</span>-->
    <button @click="openModal"  v-bind:class="['M-'+kingdom.R+kingdom.G+kingdom.B, ]" v-bind:style="{backgroundColor: kingdomColor(kingdom.R,kingdom.G,kingdom.B),borderColor: kingdom.portals?'#ffffff':'#000000'}" >
      <!--<span><img src="@/assets/rug-token.png"/> {{kingdom.balanceOf}}</span>
      <span><img src="@/assets/pyzza.png"/> {{kingdom.dividends}}</span>
      <img :src="this['_'+kingdom.R+kingdom.G+kingdom.B]"/>-->
      <div class="mascot" v-bind:class="['K-'+kingdom.R+kingdom.G+kingdom.B]" ></div>
      
      <span id="fomo-timer" class="timer-timer" v-if="TIMESTAMP < kingdom.timerEnds || kingdom.address == rugsToWishes_floatingCastleAddress">
        <span><span class="v">&Xi;</span>{{kingdom.fomoTotal}}</span><br>
        <span  v-if="TIMESTAMP< kingdom.timerEnds"><span class="v">  &#128338;</span>{{ formatTime( kingdom.timerEnds - TIMESTAMP) }}</span>
      </span>
      <span id="cooldown-timer" class="timer-timer" v-if="TIMESTAMP < kingdom.cooldown"><span class="v">&#9876;</span>{{ formatTime(kingdom.cooldown - TIMESTAMP )}}</span> <!-- -->
      <!-- 
        ingdom-block" v-for="(kingdom, k) in rugsToWishes_kingdoms" v-bind:key="kingdom.address"  v-if="rugsToWishes_floatingCastleAddress != kingdom.address" :rugs="kingdom.balanceOf" :pizzaDivs="kingdom.dividends" :baseFomo="kingdom.fomoTotal" timer="00:00"  :rgb="kingdomColor(kingdom.R,kingdom.G,kingdom.B)" v-bind:style="{ left: kingdom.X*96+'px',  top: kingdom.Y*96+'px'}" :modal="function(){openKingdomBox(kingdom)}
      -->
      <div class="caravan-box" v-if="kingdom.caravan">
        <img class="caravan-icon" src="@/assets/kingdoms/magic-lamp.png" />
      </div>
      <button v-if="parseInt(yourResolves)> kingdom.lifeForce && kingdom.R == rugsToWishes_ResolveClusterR && kingdom.G == rugsToWishes_ResolveClusterG && kingdom.B == rugsToWishes_ResolveClusterB && kingdom.king.toLowerCase() != currentAddress.toLowerCase()"  id="claimable-box"  v-bind:style="{backgroundColor: kingdomColor(kingdom.R,kingdom.G,kingdom.B)}"><img class="caravan-icon" src="@/assets/kingdoms/fight.png" /></button>

      <button @click="portals" v-if="kingdom.portals && kingdom.king.toLowerCase() == currentAddress.toLowerCase()"  class="tornado-box"  v-bind:style="{backgroundColor: kingdomColor(kingdom.R,kingdom.G,kingdom.B)}"><img class="caravan-icon" src="@/assets/kingdoms/tornado.png" /></button>
      

      
    </button>
    
    <button @click="rugsToWishes_attack(0)" id="attack-up" v-if="kingdom.Y>1 && TIMESTAMP >= kingdom.cooldown && kingdom.king.toLowerCase() == currentAddress.toLowerCase() && rugsToWishes_floatingCastleAddress != kingdom.address"  class="tornado-box"  v-bind:style="{backgroundColor: kingdomColor(kingdom.R,kingdom.G,kingdom.B)}"><img class="caravan-icon sword" src="@/assets/kingdoms/fight.png" /></button>
    <button @click="rugsToWishes_attack(2)" id="attack-down" v-if="kingdom.Y<8 && TIMESTAMP >= kingdom.cooldown && kingdom.king.toLowerCase() == currentAddress.toLowerCase() && rugsToWishes_floatingCastleAddress != kingdom.address"  class="tornado-box"  v-bind:style="{backgroundColor: kingdomColor(kingdom.R,kingdom.G,kingdom.B)}"><img class="caravan-icon sword" src="@/assets/kingdoms/fight.png" /></button>
    <button @click="rugsToWishes_attack(3)" id="attack-left" v-if="kingdom.X>1 && TIMESTAMP >= kingdom.cooldown && kingdom.king.toLowerCase() == currentAddress.toLowerCase() && rugsToWishes_floatingCastleAddress != kingdom.address"  class="tornado-box"  v-bind:style="{backgroundColor: kingdomColor(kingdom.R,kingdom.G,kingdom.B)}"><img class="caravan-icon sword" src="@/assets/kingdoms/fight.png" /></button>
    <button @click="rugsToWishes_attack(1)" id="attack-right" v-if="kingdom.X<8 && TIMESTAMP >= kingdom.cooldown && kingdom.king.toLowerCase() == currentAddress.toLowerCase() && rugsToWishes_floatingCastleAddress != kingdom.address"  class="tornado-box"  v-bind:style="{backgroundColor: kingdomColor(kingdom.R,kingdom.G,kingdom.B)}"><img class="caravan-icon sword" src="@/assets/kingdoms/fight.png" /></button>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  import hodlConvert from 'convert-seconds';

export default {
  created(){
  },
  name: 'kingdom',
    /*data(){
      animalImages = {}
      [0,1,2,3].forEach(function(r){
        [0,1,2,3].forEach(function(g){
          [0,1,2,3].forEach(function(b){
            let x = ''+r+g+b;
            animalImages['_'+x] = require('@/assets/kingdoms/'+x+'.png')
          })
        })
      })    
      return animalImages
   },*/
  props:[
    'kingdom',
    'rugs',
    'pizzaDivs',
    'baseFomo',
    'timer',
    'rgb',
    'modal'
  ],
  methods:{
    openModal:function(){
      this.modal();
    },
    rugsToWishes_attack:function(direction){
      console.log("this is it")
      //rugsToWishes_attack
      this.$store.dispatch("rugsToWishes_attack",{K:this.kingdom,D:direction})
      event.stopPropagation();
    },
    portals:function(event){
      
      this.$store.dispatch("rugsToWishes_portals", this.kingdom)
      event.stopPropagation();
    }, formatTime(hodl){
  //
  //console.log("hodl",hodl)
  /*

███████╗░█████╗░██████╗░███╗░░░███╗░█████╗░████████╗  ████████╗██╗███╗░░░███╗███████╗
██╔════╝██╔══██╗██╔══██╗████╗░████║██╔══██╗╚══██╔══╝  ╚══██╔══╝██║████╗░████║██╔════╝
█████╗░░██║░░██║██████╔╝██╔████╔██║███████║░░░██║░░░  ░░░██║░░░██║██╔████╔██║█████╗░░
██╔══╝░░██║░░██║██╔══██╗██║╚██╔╝██║██╔══██║░░░██║░░░  ░░░██║░░░██║██║╚██╔╝██║██╔══╝░░
██║░░░░░╚█████╔╝██║░░██║██║░╚═╝░██║██║░░██║░░░██║░░░  ░░░██║░░░██║██║░╚═╝░██║███████╗
╚═╝░░░░░░╚════╝░╚═╝░░╚═╝╚═╝░░░░░╚═╝╚═╝░░╚═╝░░░╚═╝░░░  ░░░╚═╝░░░╚═╝╚═╝░░░░░╚═╝╚══════╝
  */
  let hodlC = hodlConvert(hodl);
  if(hodlC.hours>24*3){
    let hourLeft = hodlC.hours % 24;
    let days = Math.floor(hodlC.hours / 24);

    return days + 'Day'+(days>1?'s':'')+ ((days<=3)?(' ' +hourLeft + 'H'):'')
  }
  else{
          if(hodl>60*60)
            return ( (hodlC.hours>0)?(hodlC.hours + 'H '):'') +hodlC.minutes + 'M'
          else{
            if(hodl>60*10)
              return hodlC.minutes + 'M'
            else{
              return (hodlC.minutes>0?(hodlC.minutes + 'M ' ):''  )+ (hodl%60) + ''
            }
          }
        }
},
/*

██╗░░██╗██╗███╗░░██╗░██████╗░██████╗░░█████╗░███╗░░░███╗  ░█████╗░░█████╗░██╗░░░░░░█████╗░██████╗░
██║░██╔╝██║████╗░██║██╔════╝░██╔══██╗██╔══██╗████╗░████║  ██╔══██╗██╔══██╗██║░░░░░██╔══██╗██╔══██╗
█████═╝░██║██╔██╗██║██║░░██╗░██║░░██║██║░░██║██╔████╔██║  ██║░░╚═╝██║░░██║██║░░░░░██║░░██║██████╔╝
██╔═██╗░██║██║╚████║██║░░╚██╗██║░░██║██║░░██║██║╚██╔╝██║  ██║░░██╗██║░░██║██║░░░░░██║░░██║██╔══██╗
██║░╚██╗██║██║░╚███║╚██████╔╝██████╔╝╚█████╔╝██║░╚═╝░██║  ╚█████╔╝╚█████╔╝███████╗╚█████╔╝██║░░██║
╚═╝░░╚═╝╚═╝╚═╝░░╚══╝░╚═════╝░╚═════╝░░╚════╝░╚═╝░░░░░╚═╝  ░╚════╝░░╚════╝░╚══════╝░╚════╝░╚═╝░░╚═╝
*/
    kingdomColor(R,G,B){
      function componentToHex(c) {
        var hex = c.toString(16);
        return hex.length == 1 ? "0" + hex : hex;
      }

      function rgbToHex(r, g, b) {
        return "#" + componentToHex(r) + componentToHex(g) + componentToHex(b);
      }
      let cluster = 255/3;
      if(R != 6)
        return rgbToHex( parseInt(cluster * R) , parseInt(cluster * G), parseInt(cluster * B) );
      else
        return rgbToHex( parseInt(255/2),parseInt(255/2),parseInt(255/2));
    }
  },
    computed:{
      ...mapState([
        "TIMESTAMP",
        "rugsToWishes_PiZZaClusterR",
        "rugsToWishes_PiZZaClusterG",
        "rugsToWishes_PiZZaClusterB",
        "rugsToWishes_caravanColor",
        "rugsToWishes_ResolveClusterR",
        "rugsToWishes_ResolveClusterG",
        "rugsToWishes_ResolveClusterB",
        "currentAddress",
        "yourResolves",
        "rugsToWishes_floatingCastleAddress"
      ])
    }
        /*,
  data(){return{
    pyzza: bondsIcon:require('@/assets/pyzza.png')
  }}*/
}

</script>

