			<modal id="governance-box" name="governance-box" width="640" >
		        <div class="modal-boxxer">
		  				<div class="hex">
		  					<div class="governance_configuration">
		  						Round Table size: {{oracleConfigurations_roundTableSize}}
		  						<input title="Vote for Round Table Seats" placeholder="e.g. 15" v-model="opt_roundtableSize"/>
