		  						<input title="Vote for Delegate Reward Share" placeholder="e.g. 30(%)" v-model="opt_delegateRS"/>
		  						<contract-data label="Your Suggestion" :value="showNumber(yourOracleConfigVotes_delegateRS,1e18,0) +'%'" />
		  					</div>
